window.addEventListener("load", function () {
  window.cookieconsent.initialise({
    type: "opt-in", // This adds "Allow" and "Deny" buttons
    palette: {
      popup: {
        background: "#000"
      },
      button: {
        background: "#f1d600"
      }
    },
    theme: "classic",
    content: {
      message: "This website uses cookies to improve user experience.",
      allow: "Allow cookies",
      deny: "Deny cookies",
      link: "Learn more",
      href: "/privacy-policy" // replace with your privacy policy URL
    },
    onInitialise: function (status) {
      if (this.hasConsented()) {
        console.log("âœ… Cookies allowed");
        // Run your analytics or tracking code here
      } else {
        console.log("ðŸš« Cookies denied");
        // Disable or skip analytics
      }
    }
  });
});


  document.addEventListener("DOMContentLoaded", function () {
    const revokeBtn = document.querySelector(".cc-revoke.cc-bottom");

    if (revokeBtn) {
      let isVisible = false;

      revokeBtn.addEventListener("click", function () {
        isVisible = !isVisible;
        revokeBtn.style.transform = isVisible ? "translateY(0)" : "translateY(90%)";
        revokeBtn.style.opacity = isVisible ? "1" : "0.6";
      });
    }
  });

